body {
  height: 100vh;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

#box {
  width: 200px;
  height: 50px;
  background-color: aliceblue;
  border-radius: 1rem;
  border: dashed;
  padding: 1rem;
}

#inner-box {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: black;
  position: relative;
}

#box:hover #inner-box {
  animation: run 1s forwards;
}

@keyframes run {
  0% {
    left: 0;
  }
  100% {
    left: 100px;
  }
}